import React from "react";
import { observer } from "mobx-react";
import Typography from "@material-ui/core/Typography";
import StarIcon from "@material-ui/icons/Star";
import ThumbDownIcon from "@material-ui/icons/ThumbDown";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import VideoIcon from "@material-ui/icons/VideoCall";
import PersonIcon from "@material-ui/icons/Person";
import TabletIcon from "@material-ui/icons/Tablet";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import ButtonGroup from "@material-ui/core/ButtonGroup";

function Help({ store }) {
  function update() {
    document.location.reload();
  }

  function startColor(count) {
    store.setColorCount(count);
    store.setRunningApp("COLOR");
  }

  return (
    <Paper className="help">
      <Typography paragraph>
        Hr hittar du hjlp med hur appen fungerar.
      </Typography>
      <Typography paragraph>
        Du kollar just nu p{" "}
        <span style={{ color: "red" }}>
          {store.viewSeasonObject.friendlyName}
        </span>
      </Typography>
      <Typography paragraph>Visa annan ssong:</Typography>

      <ButtonGroup color="primary" aria-label="Outlined primary button group">
        {store.seasonsWithoutView.map(season => (
          <Button
            key={season.season}
            onClick={() => store.switchSeason(season)}
          >
            {season.friendlyName}
          </Button>
        ))}
      </ButtonGroup>
      <Typography paragraph />
      <Typography paragraph>
        Varje ssong har 16 utmaningar. Man vljer vilken utmaning som helst,
        man behver inte gra dem i ordning. Utmaning filmar man sen i
        horisontellt lge med kameran. Nr man r njd s klickar man p ladda
        upp p den utmaningen man gjort. Filmen hamnar d fr utvrdering, den
        r d bara synlig fr dig. Om den blir godknd s kommer den synas fr
        vriga deltagare.
      </Typography>

      <Typography paragraph>
        Tnk p att ni kan filma er sjlva genom att stta upp kameran
        ngonstanns , sen stter ni igng med utmaningen. Nr ni r klara
        stnger ni av videon och klipper brjan och slutet som ni inte vill ha
        med. Det kan ni enkelt gra i bilderappen. Sen gr ni in och laddar upp
        den p rtt utmaning. P s stt behver ni inte hjlp med att ngon
        filmar er.
      </Typography>

      <Typography paragraph>
        Utmaningarna r i fyra kategorier.
      </Typography>

      <Typography paragraph>
        BAL = BALL MASTERY. Hr gller det att kunna klara av att hantera bollen p ett smidigt stt.
      </Typography>

      <Typography paragraph>
        THE - THECNICAL. Hr r det mycket kickningar och trix.
      </Typography>

      <Typography paragraph>
        DRI - DRIBBLE. Hr gller det att f till olika finter och driva bollen
      </Typography>

      <Typography paragraph>
        PSY - PHYSICAL. Hr handlar det om att bli starkare, men ven en del skottvningar som krver styrka och teknik.
      </Typography>

      <Typography variant="h6" paragraph>
        <HomeIcon /> Hem
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr visas fldet med alla ssongens anvndaruppladde filmer.
        </Typography>
        <Typography paragraph>
          <AccountCircleIcon />
          Under utvrdering fr att se om man klarat det.
        </Typography>
        <Typography paragraph>
          <StarIcon />
          Klarad utmaning
        </Typography>
        <Typography paragraph>
          <ThumbDownIcon />
          Utmaning misslyckad. Bara att frska igen. Kanske hade du inte flytet
          i utmaningen, eller missfrttt ngot. Kontrollera videon och
          instruktionerna och frsk igen.
        </Typography>
        <Typography paragraph>
          Om du klickar p personens namn s kan du se profilen.
        </Typography>
        <Typography paragraph>
          Klicka p videon fr att se den i helskrm.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <StarIcon /> Utmaningar
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr visas fldet med alla ssongens utmaningar. Det finns 3 olika
          niver. Ltt, medium, svr.
        </Typography>
        
        <Typography paragraph>
          <VideoIcon />
          Klicka p denna fr att ladda upp en film. Filma alltid i horisontellt
          lge.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <StarIcon /> Topplistan
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Varje ssong bestr av maximalt 200 pong. Hr visas vem som ftt
          flest pong.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <PersonIcon /> Profil
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr kan du ladda upp din profilbild och andra anvndarinstllningar.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <TabletIcon /> Verktyg
      </Typography>

      {/* <Button variant="outlined" onClick={() => store.setRunningApp("NUMBER")}>
        Nummer
      </Button>

      <Button variant="outlined" onClick={() => startColor(2)}>
        Color 2 frger
      </Button>

      <Button variant="outlined" onClick={() => startColor(4)}>
        Color 4 frger
      </Button> */}

      <Typography paragraph>
        Om det finns nya uppdateringar kan du uppdatera hr.
      </Typography>

      <Button variant="outlined" onClick={update}>
        Uppdatera appen
      </Button>
    </Paper>
  );
}

export default observer(Help);
